.nav-bar {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 25%;
  background-color: rgb(252 211 77 / 0.9);
  display: flex;
  flex-direction: column;
}

:is(.dark .nav-bar) {
  --tw-bg-opacity: 1;
  background-color: rgb(23 23 23 / var(--tw-bg-opacity));
}

@media (min-width: 1180px) {
  .nav-bar {
    z-index: 0;
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    display: flex;
    flex-direction: row;
  }
}

.real-nav {
  height: 100%;
  justify-content: space-between;
  background-color: rgb(251 191 36 / 0.7);
  display: flex;
  flex-direction: column;
}

@media (min-width: 1180px) {
  .real-nav {
    align-items: center;
    justify-content: normal;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    padding-right: 5rem;
    padding-left: 1.25rem;
    display: flex;
    flex-direction: row;
  }
}

.real-nav ul {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1180px) {
  .real-nav ul {
    margin-top: 0px;
    margin-bottom: 0px;
    align-items: center;
    -webkit-column-gap: 2.5rem;
            column-gap: 2.5rem;
    display: flex;
    flex-direction: row;
  }
}

.real-nav ul .nav-text {
  display: block;
  width: 100%;
  border-bottom-width: 2px;
  border-color: rgb(245 158 11 / 0.5);
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
  font-size: 1.5rem;
  line-height: 2rem;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.real-nav ul .nav-text:hover {
  background-color: rgb(245 158 11 / 0.4);
}

@media (min-width: 1180px) {
