.creator-pallet {
  margin-top: 1rem;
  margin-bottom: 1rem;
  align-items: center;
  display: flex;
  flex-direction: column-reverse;
}

.creator-pallet::after {
  font-size: 1.25rem;
  line-height: 1.75rem;
  --tw-content: 'Created by';
  content: var(--tw-content);
}

:is(.dark .creator-pallet) {
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

@media (min-width: 1180px) {
  .creator-pallet {
    margin-top: 0px;
    margin-bottom: 0px;
    align-items: center;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    font-size: 1.5rem;
    line-height: 2rem;
    display: flex;
    flex-direction: row;
  }

  .creator-pallet::before {
    --tw-content: 'Created by';
    content: var(--tw-content);
  }

  .creator-pallet::after {
    --tw-content: none;
    content: var(--tw-content);
  }
}

.creator-pallet img {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  height: 5rem;
  width: 5rem;
  border-radius: 9999px;
}

